//#region Global Imports
import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import {
    Grid, 
    Typography,
    Tabs, 
    Tab
} from '@material-ui/core/';
import { 
    withStyles 
} from '@material-ui/core/styles';
import clsx from 'clsx';

//#region Local Imports

import { API } from '../API';
import './style.scss';

//#endregion Local Imports
import { Contract } from './Contract';

//#region Interface Imports
import { Store } from 'Components/Stores/IStore';
import { IMemberContractPage } from './IMemberContractPage';
import { MemberContractPageActions } from './Actions';
import { Loader } from 'Components/UI/LoadingIndicator';
import { Components } from '@hims/core';
//#endregion Interface Imports

import '@hims/core/dist/index.css';

/*
const page_tab_titles: any = [
    "Benefits",
    "Exclusions",
    "Partner Network",
    "Contract",
]
*/
const page_tab_titles: any = [
    "Contract",
]

const MemberContractTabs = withStyles({
    root: {
    },
    indicator: {
        display: 'box',
        marginTop: 4,
        height: 3,
        backgroundColor: '#3AB77D',
    },
})(Tabs);
  
const MemberContractTab = withStyles({
    root: {
        color: 'rgba(39, 46, 76, 0.45)',
        paddingLeft: 32,
        paddingRight: 32,
        fontSize: '1rem',
        '&:hover': {
            color: 'rgba(39, 46, 76, 1)',
        },
        '&$selected': {
            textDecoration: 'none',
            color: 'rgba(39, 46, 76, 1)',
            fontWeight: 600,
        }
    },
    selected: {},
})(Tab);

export class MemberContractPage extends React.Component<IMemberContractPage.IProps, IMemberContractPage.IState> {
    
    constructor(props: IMemberContractPage.IProps) {
        super(props);

		this.state = {
            loading: false,
            selectedTab: 0,
		    memberData: null,
            contractData: null,
            clientName: "",
            memberName: "",
            memberCardNo: "",
            contractName: "",
        };
        
        this.setLoading = this.setLoading.bind(this);
        this.setSelectedTab = this.setSelectedTab.bind(this);
        
	}

	reset = () => {
		this.setState({
            loading: false,
            selectedTab: 0,
            clientName: "",
		    memberName: "",
            memberCardNo: "",
            contractName: "",
		});
    };

    setSelectedTab = (tab) => {
        this.setState({
            selectedTab: tab
        });
    }

    handleChange = (event:any, newValue:number) => {
        console.log("event >>>", event);
        this.setSelectedTab(newValue);
    }

    setLoading = (isLoading) => {
        this.setState({
            loading: isLoading
        });
    }

    componentDidMount = () => {
        let ins = this;

        ins.setState({
            loading: true
        });
        API.getMemberData(ins.props.match.params.member_id).then(response => {
			if(response && response.client_id && response.contracts.length > 0){
                let firstName = response && response.first_name ? response.first_name : '';
                let mi = response && response.middle_name ? response.middle_name.charAt(0) + '.' : '';
                let lastName = response && response.last_name ? response.last_name : '';
                let client_id = response.client_id;
                let contract_id = response.contracts[0];
                let memberCardNo = response.member_card_no ? response.member_card_no : '';
                ins.setState({
                    memberData: response,
                    memberName: firstName+' '+mi+' '+lastName,
                    memberCardNo: memberCardNo
                }, () => {
                    API.getClientContracts(client_id).then(response2 => {
                        if(response2 && response2.length == 1){
                            let clientDtls = response2[0];
                            let clientName = clientDtls.registered_name ? clientDtls.registered_name : '';
                            let contractData = null;
                            try{
                                contractData = clientDtls.contract_objects.find(({ _id }) => _id === contract_id);
                            }catch(err){}
                            if(contractData){
                                let contractName = contractData['contract_name'] ? contractData['contract_name'] : '';
                                ins.setState({
                                    loading: false,
                                    clientName: clientName,
                                    contractData: contractData,
                                    contractName: contractName
                                });
                            }
                            else{
                                ins.setState({
                                    loading: false
                                });
                            }
                        }
                        else{
                            ins.setState({
                                loading: false
                            });
                        }
                    });
                });
            }
            else{
                ins.setState({
                    loading: false
                });
            }
        });
    }
    public render(): JSX.Element {
		const {
            loading,
            selectedTab,
		    clientName,
            memberName,
            memberCardNo,
            contractName,
        } = this.state;

        let breadcrumbs_items = [
            {
                label: 'Members',
                url: '#/membership' 
            },
            {
                label: clientName,
                url: '#/membership/members' 
            },
            {
                label: memberName,
                url: '#/membership/view-member/'+this.props.match.params.member_id
            },
            {
                label: memberCardNo,
                url: '#/membership/view-member/'+this.props.match.params.member_id
            },
            {
                label: contractName,
                url: ''
            }
        ]

        const BlankTemplate = () => {
    
            return (
                <Grid container>
                    <Grid item xs={12}>
                        <Typography>Test</Typography>
                    </Grid>
                </Grid>
            );
        };

        return (
            <div className={clsx('MemberContractPage')}>
                { loading ?
                    (<Loader />) : null
                }
			    <Grid container>
                    <Grid item xs={12}>
                        <Grid container className={clsx('page-header')}>
                            <Grid item xs={12}>
                                <Components.UI.BreadcrumbsComponent items={breadcrumbs_items} />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography className={clsx('member-contract-header')}>{contractName}</Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} className={clsx('page-tab-title')}>
                        <MemberContractTabs value={selectedTab} onChange={this.handleChange} aria-label="page tab">
                            { page_tab_titles.map((title: string, index: number) => (
                                <MemberContractTab
                                    label={
                                        title
                                    }
                                    id={"member_contract_tab_"+index}
                                    aria-controls={"member-contract-tabpanel-"+index}
                                    key={index}
                                />
                                )
                            )}
                        </MemberContractTabs>
                        <div className={clsx('tab-title-shadow')} />
                    </Grid>
                    <Grid item xs={12} className={clsx('page-tab-content')}>
                        { 
                            selectedTab === page_tab_titles.indexOf('Benefits') ?
                                <BlankTemplate />
                            : selectedTab === page_tab_titles.indexOf('Exclusions') ?
                                <BlankTemplate />
                            : selectedTab === page_tab_titles.indexOf('Partner Network') ?
                                <BlankTemplate />
                            : selectedTab === page_tab_titles.indexOf('Contract') ?
                                <Contract contract_name={'2018 Contract'} contract_id={'5d9af397ddf3ea03585a37ae'} />
                            : null
                        }
                    </Grid>
                </Grid>
            </div>
	    );
    }

}

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(MemberContractPageActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };