    static getMemberDependents(member_first_name: string, member_last_name: string){
        let GET_DEPENDENTS_URL = API_URL+'members';

        const filter = {
            where:{
                and: [
                    {
                        principal_first_name: member_first_name,
                    },
                    {
                        principal_last_name: member_last_name,
                    }
                ]
            }
        }

        GET_DEPENDENTS_URL = GET_DEPENDENTS_URL + '?filter=' + JSON.stringify(filter);

        return fetch(GET_DEPENDENTS_URL, {
            method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
        });
    }

    static patchMemberDependents(keys: string[], payloads: any){
        let ROOT_MEMBER_EDIT_URL = API_URL+'members/';

        return keys.map(key => {
            return new Promise((resolve) => {
                fetch(ROOT_MEMBER_EDIT_URL+key, {
                    method: 'PATCH',
                    headers: {
                        'Content-Type':'application/json',
                        'Authorization': 'Bearer ' + localStorage.getItem('pm_token'),
                    },
                    body: JSON.stringify(payloads[key])
                }).then(
                    () => {
                        resolve('');
                    }
                ).catch(error => {
                    resolve(error.message);
                });
            });
        });
    }

