import * as React from 'react';
import { useState, useRef } from 'react';
import { 
    Grid, 
    Typography, 
    Paper, 
    Popper,
    ClickAwayListener,
    Box,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableRow,
} from '@material-ui/core/';
import { Link } from 'react-router-dom';
import { 
    makeStyles, 
} from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faUserCog
} from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';

const hmoitemStyles = makeStyles({
    hmoname: {
      fontSize: '0.75rem',
      color: 'rgba(39, 46, 76, 0.6)',
      textTransform: 'uppercase',
      paddingRight: 32
    },
    hmovalue: {
      fontSize: '1rem',
      fontWeight: 600,
      color: 'rgb(39, 46, 76)',
    },
    hmovaluelink: {
        fontSize: '1rem',
        fontWeight: 600,
        color: 'rgb(39, 46, 76)',
        textDecoration: 'underline',
        lineHeight: 1.5
    },
});

interface HMOItemProps {
    name?: string;
    value?: string;
    link?: string;
}

const HMOItem: React.FC<HMOItemProps> = (
	props: HMOItemProps,
): JSX.Element => {
    const classes = hmoitemStyles(props);

    return(
        <Grid container alignItems="center" style={{paddingTop: 4, paddingBottom: 4}}>
            <Grid item xs={5}>
                <Typography className={classes.hmoname}>{props.name}</Typography>
            </Grid>
            <Grid item xs={7}>
                {
                    props.link ?
                    <Link to={props.link} className={classes.hmovaluelink}>{props.value}</Link>
                    : <Typography className={classes.hmovalue}>{props.value}</Typography>
                }
            </Grid>
        </Grid>
    )
}

interface HMOInformationProps {
    data?: any;
    handleShowModal: (status: string) => void;
    dependents: any[];
    clientName?: string;
    contractData?: any;
}

const hmoinfoStyles = makeStyles(theme => ({
    subtitle: {
        fontSize: '1.125rem',
        fontWeight: 600,
    },
    hmocheckboxlabel: {
        fontSize: '0.75rem',
        color: 'rgb(39, 46, 76)',
    },
    hmographsubtitle: {
        fontSize: '0.75rem',
        textTransform:'uppercase',
        color: 'rgb(39, 46, 76, 0.6)',
        marginBottom: 16,
    },
    hmodependentsbox: {
        height: 175,
		borderRadius: 0,
		overflow: 'auto',
        boxShadow: '0px 3px 6px #272E4C19',
        padding: 0,
    },
    hmoremarksbox: {
        height: 175,
        borderRadius: 8,
        backgroundColor: '#FFFFFF',
        overflow: 'hidden',
        border: '1px solid #272E4C4D',
    },
    hmodependentsbox_link: {
        color: 'rgb(39, 46, 76)',
    },
    change_status_link: {
        color: '#0D5E40',
        cursor: 'pointer',
        fontSize: '13px',
        textDecoration: 'underline'
    },
    icons: {
        color: '#0D5E40',
        cursor: 'pointer',
        fontSize: '13px',
    },
    popper:{
        top: '4px',
        '&[x-placement*="bottom"] $arrow': {
            top: 0,
            left: 0,
            marginTop: '-0.9em',
            width: '3em',
            height: '1em',
            '&::before': {
              borderWidth: '0 1em 1em 1em',
              borderColor: `transparent transparent ${theme.palette.background.paper} transparent`,
            }
        },
        '&[x-placement*="top"] $arrow': {
            bottom: 0,
            left: 0,
            marginBottom: '-0.9em',
            width: '3em',
            height: '1em',
            '&::before': {
              borderWidth: '1em 1em 0 1em',
              borderColor: `${theme.palette.background.paper} transparent transparent transparent`,
            }
        },
        '&[x-placement*="right"] $arrow': {
            left: 0,
            marginLeft: '-0.9em',
            height: '3em',
            width: '1em',
            '&::before': {
              borderWidth: '1em 1em 1em 0',
              borderColor: `transparent ${theme.palette.background.paper} transparent transparent`,
            }
          },
        '&[x-placement*="left"] $arrow': {
            right: 0,
            marginRight: '-0.9em',
            height: '3em',
            width: '1em',
            '&::before': {
              borderWidth: '1em 0 1em 1em',
              borderColor: `transparent transparent transparent ${theme.palette.background.paper}`,
            }
        }
    },
    arrow: {
        position: 'absolute',
        fontSize: 7,
        width: '3em',
        height: '3em',
        '&::before': {
          content: '""',
          margin: 'auto',
          display: 'block',
          width: 0,
          height: 0,
          borderStyle: 'solid'
        }
    },
    paper: {
        padding: '15px',
        width: 110
    },
    status: {
        padding: '5px',
        cursor: 'pointer'
    }
}));

export const HMOInformation: React.FC<HMOInformationProps> = (
	props: HMOInformationProps,
): JSX.Element => {
    const classes = hmoinfoStyles(props);
    const { 
        data,
        dependents,
        clientName,
        contractData, 
    } = props;
    const anchorRef = useRef(null);
    const [arrowRef, setArrowRef] = useState<any>(null);
    const [isPopoverOpen, setPopupoverOpen] = useState(false);
    
    function openPopover(){
        // setSuspendMemberModalOpen(true);
        setPopupoverOpen(true);
    }

    function closePopover(){
        setPopupoverOpen(false);   
    }

    function openActiveMemberModal(){
        setPopupoverOpen(false);
        props.handleShowModal('active');
    }

    function openSuspendMemberModal(){
        setPopupoverOpen(false);
        props.handleShowModal('suspend');
    }

    function openTerminatedMemberModal(){
        setPopupoverOpen(false);
        props.handleShowModal('terminate');
    }

    function isValidDate(date: string){
        const d = new Date(date);
        return d instanceof Date && !isNaN(d.getTime());
    }
    
    return(
        <Grid container style={{paddingLeft: 32}}>
            <Grid item xs={12} style={{paddingTop: 16, paddingBottom: 16}}>
                <Grid container direction="row" justify="space-between">
                    <Grid item xs={8}>
                        <Typography className={classes.subtitle} color="textPrimary">
                            {clientName}
                        </Typography>
                    </Grid>
                    
                    <Grid item xs={4} >
                        <Grid container direction="row" justify="flex-end">
                            <Grid item>
                                <span 
                                    ref={anchorRef}
                                    className={classes.change_status_link}
                                    onClick={openPopover}>
                                    <FontAwesomeIcon 
                                        icon={faUserCog}
                                        className={classes.icons}
                                    /> Change Status
                                    <Popper
                                        open={isPopoverOpen}
                                        anchorEl={anchorRef.current}
                                        placement={"bottom-end"}
                                        className={classes.popper}
                                        disablePortal={false}
                                        style={{marginTop:'4px'}}
                                        modifiers={{
                                            flip: {
                                              enabled: false,
                                            },
                                            preventOverflow: {
                                              enabled: true,
                                              boundariesElement: 'undefined',
                                            },
                                            arrow: {
                                              enabled: true,
                                              element: arrowRef,
                                            },
                                        }}
                                    >
                                        <span className={classes.arrow} ref={setArrowRef} />
                                        <Paper className={classes.paper}>
                                            <ClickAwayListener onClickAway={closePopover}>
                                                <Grid container>
                                                    <Grid item xs={12}>
                                                        <Typography className={classes.status} onClick={openActiveMemberModal}>
                                                            Active
                                                        </Typography>
                                                    </Grid>
                                                    <Grid item xs={12}>
                                                        <Typography className={classes.status} onClick={openSuspendMemberModal}>
                                                            Suspended
                                                        </Typography>
                                                    </Grid>
                                                    <Grid item xs={12}>
                                                        <Typography className={classes.status} onClick={openTerminatedMemberModal}>
                                                            Terminated
                                                        </Typography>                                                 
                                                    </Grid>
                                                </Grid>
                                            </ClickAwayListener>
                                        </Paper>
                                    </Popper>
                                </span>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOItem name="Member Account No." value="-" />
                <HMOItem name="Member ID" value={(data && data.member_id ? data.member_id : '-')} />
                <HMOItem 
                    name="Contract" 
                    value={contractData && contractData['contract_name'] ? contractData['contract_name'] : (data ? data.contracts[0] : '-')}
                    link={data ? '/membership/member-contract/'+data['_id'] : '#'} />
                <HMOItem name="Effectivity Date" value={data && isValidDate(data.effectivity_date) ? moment(data.effectivity_date).format('MMMM D, YYYY') : '-'} />
                <HMOItem name="Contract Renewal Date" value="-" />
                <HMOItem name="Contract Expiration Date" value={data && isValidDate(data.termination_date) ? moment(data.termination_date).format('MMMM D, YYYY') : '-'} />
                <HMOItem name="VIP Member" value={data? (data.is_vip ? 'Yes' : 'No' ) : '-'} />
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOItem name="Type" value={(data && data.member_type ? data.member_type : '-')} />
                <HMOItem name="Plan Type" value={(data && data.plan_type ? data.plan_type : '-')} />
                <HMOItem name="MBL" value="-" />
                <HMOItem name="ABL" value="-" />
                <HMOItem name="PEC" value="-" />
                <HMOItem name="Philhealth Rider" value={data? (data.is_philhealth_rider ? 'Yes' : 'No' ) : '-'} />
                <HMOItem name="Batch Process No." value="-" />
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOItem name="Date of Birth" value={data && isValidDate(data.date_of_birth) ? moment(data.date_of_birth).format('MMMM D, YYYY') : '-'} />
                <HMOItem name="Age" value={data && isValidDate(data.date_of_birth) ? ''+(moment().diff(data.date_of_birth, 'years', false)) : '-'} />
                <HMOItem name="Gender" value={data ? data.gender : '-'} />
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOItem name="Civil Status" value={data ? data.civil_status : '-'} />
                <HMOItem name="Title/Prefix" value={data && data.title ? data.title : '-'}/>
                <HMOItem name="Suffix" value={data && data.suffix ? data.suffix : '-'} />
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 48, paddingBottom: 48, paddingRight: 16}}>
                <Typography className={classes.hmographsubtitle}>Enrolled Dependents</Typography>
                <Paper className={classes.hmodependentsbox}>
                    <Table size="small" >
                        <TableHead>
                            <TableRow>
                                <TableCell>Dependent Name</TableCell>
                                <TableCell>Relationship</TableCell>
                                <TableCell>Status</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                        {
                            dependents.map((row, index) => (
                                <TableRow key={index}>
                                    <TableCell component="th" scope="row">
                                        <Link to={'#'} className={classes.hmodependentsbox_link}>
                                            {row['first_name']+' '+(row['middle_name'] ? row['middle_name'].charAt(0) + '. ' : '')+row['last_name']}
                                        </Link>
                                    </TableCell>
                                    <TableCell>
                                        {row['relationship_to_principal']}
                                    </TableCell>
                                    <TableCell>
                                        {row['member_status']}
                                    </TableCell>
                                </TableRow>
                            ))
                        }
                        </TableBody>
					</Table>
                </Paper>
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 48, paddingBottom: 48}}>
                <Typography className={classes.hmographsubtitle}>Remarks</Typography>
                <Box className={classes.hmoremarksbox}>

                </Box>
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOItem name="Corporate Acct. Name" value="-" />
                <HMOItem name="Card Name" value="-" />
                <HMOItem name="Cost Center" value="-" />
                <HMOItem name="Financial Code" value="-" />
                <HMOItem name="Site Assigned" value="-" />
                <HMOItem name="Floor" value="-" />
                <HMOItem name="Site Address" value="-" />
            </Grid>
            <Grid item xs={12} md={6} style={{paddingTop: 0, paddingBottom: 0}}>
                <HMOItem name="Designation" value="-" />
                <HMOItem name="Job Grade/Level" value={data ? data.job_lvl : '-'} />
                <HMOItem name="Supervisor" value="-" />
                <HMOItem name="Date of Hire" value={data && isValidDate(data.hire_date) ? moment(data.hire_date).format('MMMM D, YYYY') : '-'} />
                <HMOItem name="Expat" value="-" />
            </Grid>
        </Grid>
    )
}

HMOInformation.defaultProps = {
    data: {},
    dependents: [],
    clientName: "",
    contractData: {},
}