    getMemberData = () => {
        this.setState({
            loading: true
        });
        API.getMemberData(this.props.match.params.member_id).then(response => {
            console.log('get member data response: ',response);
            this.setState({
                loading: false
            });
            if(response){
                console.log("MEMBER DATA", response);
                let firstName = response && response.first_name ? response.first_name : '';
                let mi = response && response.middle_name ? response.middle_name.charAt(0) + '.' : '';
                let lastName = response && response.last_name ? response.last_name : '';
                this.setState({
                    memberData: response,
                    fullName: firstName+' '+mi+' '+lastName,
                });
            }
        });
    }

    queryStringToJSON = (queryString) => {
		if(queryString.trim().length < 1){
			return {};
		}
		
		var pairs = queryString.slice(1).split('&');
		
		var result = {};
		pairs.forEach(function(pair) {
			pair = (pair as string).split('=');
			result[pair[0]] = decodeURIComponent(pair[1] || '');
		});
	
		return JSON.parse(JSON.stringify(result));
    }

    fetchMemberLogs = () => {
        let ins  = this;
        API.getMemberLogs(ins.props.match.params.member_id).then(response => {
            if(response && response.error === undefined){
                let memberLogs = response.map((item: any) => {
                    item['revision_time'] = item.revision_date;
                    return item;
                });
                ins.setState({
                    memberLogs: memberLogs
                });
            }
        });
    }

    fetchPlanTypeOptions = (onSuccess: any, onError: any) => {
        let ins  = this;
        let contract_id = ins.state.memberData.contracts[0] || '';
        API.getPlanTypes().then(response => {
            if(response && response.error === undefined){
                let filteredPlanTypes = response.filter((item: any) => {
                    let nodepaths = item['node_path'];
                    return nodepaths.indexOf(contract_id) >= 0 && item.code !== 'BASE' && item.type === 'PlanType';
                });
                
                let planTypeOptions: ISelectItem[] = filteredPlanTypes.map((item: any, index: number) => {
                    return {
                        id: index,
                        value: item.code,
                        label: item.name,
                    }
                });

                ins.setState({
                    plan_type_options: planTypeOptions
                }, () => {
                    onSuccess();
                });
            }
            else{
                onError();
            }
        });
    }

    fetchMemberDependents = (firstname: string, lastname: string, callback: any) => {
        let ins  = this;
        API.getMemberDependents(firstname, lastname).then(response => {
            if(response && response.error === undefined){
                ins.setState({
                    memberDependents: response
                }, () => {
                    callback();
                });
            }
            else{
                callback();
            }
        });
    }
    
    componentDidMount = () => {
        let ins = this; 
        
        ins.setState({
            loading: true,
        }, () => {
            API.getMemberData(ins.props.match.params.member_id).then(response => {
                
                if(response){
                    let firstName = response && response.first_name ? response.first_name : '';
                    let mi = response && response.middle_name ? response.middle_name.charAt(0) + '.' : '';
                    let lastName = response && response.last_name ? response.last_name : '';
                    let client_id = response.client_id;
                    let contract_id = response.contracts[0];
                    ins.setState({
                        memberData: response,
                        fullName: firstName+' '+mi+' '+lastName,
                    });

                    ins.fetchMemberDependents(
                        firstName, 
                        lastName, 
                        () => {
                            
                            API.getClientContracts(client_id).then(cdresponse => {
                                if(cdresponse && cdresponse.length === 1){
                                    let clientDtls = cdresponse[0];
                                    let clientName = clientDtls.registered_name ? clientDtls.registered_name : '';
                                    let contractData = null;
                                    try{
                                        contractData = clientDtls.contract_objects.find(({ _id }) => _id === contract_id);
                                    }catch(err){}
                                    if(contractData){
                                        ins.setState({
                                            clientName: clientName,
                                            contractData: contractData
                                        });



                                        API.getMemberDocuments(ins.props.match.params.member_id).then(response3 => {

                                            if(response3 && response3.error === undefined){
                                                ins.generateDocumentData(response3, true);
                                            }

                                            ins.fetchPlanTypeOptions(
                                                function() {
                                                    //onSuccess
                                                    let propsJson = JSON.parse(JSON.stringify(ins.props));
                                                    let queryValues = ins.queryStringToJSON(propsJson.location.search);
                                                    let edit_mode = queryValues && queryValues.edit && queryValues.edit === 'true';

                                                    if(edit_mode){
                                                        let currEditMemberValues = ins._generateEditMemberValues();
                                                        ins.setState({
                                                            loading: false,
                                                            editMemberValues: currEditMemberValues,
                                                            edit_mode: true,
                                                        });
                                                    }
                                                    else{
                                                        ins.setState({
                                                            loading: false,
                                                            edit_mode: false,
                                                        });
                                                    }

                                                    ins.fetchMemberLogs();
                                                },
                                                function() {
                                                    //onError
                                                    ins.setState({
                                                        loading: false
                                                    });
                                                }
                                            );
                                        });
                                    }
                                    else{
                                        ins.setState({
                                            loading: false
                                        });
                                    }
                                }
                                else{
                                    ins.setState({
                                        loading: false
                                    });
                                }
                            });

                        }
                    );
    
                    
                }
                else{
                    ins.setState({
                        loading: false
                    });
                }
            });
        });
        
    }

