    handleIsVIPChange = () => {
        let currEditMemberValues = this.state.editMemberValues;
        let currIsVIP = currEditMemberValues['isVIP'];
        currEditMemberValues['isVIP'] = !currIsVIP;
        this.setState({
            editMemberValues: currEditMemberValues
        });
    };

    handleIsPhilhealthRiderChange = () => {
        let currEditMemberValues = this.state.editMemberValues;
        let currIsVIP = currEditMemberValues['isPhilhealthRider'];
        currEditMemberValues['isPhilhealthRider'] = !currIsVIP;
        this.setState({
            editMemberValues: currEditMemberValues
        });
    };

    handleDependentsChange = (key: string, fieldName: string, fieldValue: string) => {
        let currEditMemberValues = this.state.editMemberValues;
        if(fieldName === 'date_of_birth'){
            currEditMemberValues['dependents'][key][fieldName] = (new Date(fieldValue)).toISOString();
        }
        else{
            currEditMemberValues['dependents'][key][fieldName] = fieldValue;
        }
        this.setState({
            editMemberValues: currEditMemberValues
        });
    }
    
